<template>
  <div class="navigation-panel">
    <div v-if="!isWeb">
      <div class="full-width-name">Ankita Tank</div>
      <div class="full-width-navbar">
        <nav class="scene_nav-mobile">
          <ol class="scene_nav_list">
            <li class="scene_nav_item" v-for="(params, index) in maxScene" :key="index">
              <button
                class="scene_nav_button"
                @click="selectedView(index)"
                :class="{ 'o-active': scene-1 === index-1 }"
              ></button>
            </li>
          </ol>
        </nav>
      </div>
    </div>
    <div v-else>
      <div style="width: 100%; overflow: hidden;">
        <div class="web-name">Ankita Tank</div>
        <div class="web-navbar">
          <nav class="scene_nav-web">
            <ol class="scene_nav_list">
              <li class="scene_nav_item" v-for="(params, index) in maxScene" :key="index">
                <button
                  class="scene_nav_button"
                  @click="selectedView(index)"
                  :class="{ 'o-active': scene-1 === index-1 }"
                ></button>
              </li>
            </ol>
          </nav>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      scene: 0,
      maxScene: 4
    };
  },
  methods: {
    selectedView(index) {
      this.scene = index;
      this.$emit("newViewSelected", index);
    }
  },
  computed: {
    isWeb() {
      return window.innerWidth >= 700;
    }
  },
  mounted() {
    setTimeout(() => {
      this.scene = 0;
    }, 1000);
  }
};
</script>

